import { styled } from '@material-ui/core/styles'
import React from 'react'
import { TransparentCard } from './TransparentCard'

const StyledBackground = styled(({ color, ...other }) => <section {...other} />)({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  minHeight: '100vh',
  background: '#1c1c25',
  '&::before': {
    content: '""',
    position: 'absolute',
    bottom: '-20%',
    left: '50%',
    width: '600px',
    height: '600px',
    background: 'linear-gradient(#f00,#f0f)',
    borderRadius: '50%',
  },
})
export function Background() {
  return (
    <StyledBackground>
      <TransparentCard></TransparentCard>
    </StyledBackground>
  )
}
